@use "../../Assets/_variables.scss" as *;

.contact {
  height: 100vh;
  background-color: $green;
  width: 100%;
  display: flex;
  flex-direction: row;
  box-sizing: border-box;
  border-top: $border-height solid $black;

  &__content {
    display: flex;
    flex: 1;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
    padding: 30px;

    &-graphic {
      width: 25%;

      &-img {
        width: 100%;
        height: 100%;
      }
    }

    &-form {
      color: $white;
      font-size: 1.5em;
      font-family: $bodyFont;
      text-align: justify;
      display: flex;
      justify-content: center;
      align-content: center;
      width: 50%;
      margin: 70px;
      height: 80%;
      box-shadow: 10px 10px $black;
    }
  }

  &__greeting {
    width: 150px;
    background-color: $white;
    display: flex;
    justify-content: center;
    align-items: center;
    writing-mode: vertical-lr;

    &-title {
      font-family: $headingFont;
      font-size: 4em;
      padding: 5px;
      border-left: solid 2px $red;
    }
  }
}
